@font-face {
    font-family: "iconfont";
    src: url(../../static/media/iconfont.adc107f3.eot); /* IE9*/
    src: url(../../static/media/iconfont.adc107f3.eot#iefix) format('embedded-opentype'),  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
    url(../../static/media/iconfont.4e2a4d13.ttf) format('truetype'),  url(../../static/media/iconfont.cab162c2.svg#iconfont) format('svg'); /* iOS 4.1- */
}

* {
    margin: 0;
    padding: 0;
}

html, body, .img-sec {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

html {
    background: #f7f67b;
}

body {
    background: radial-gradient(200% 100% at bottom center, #f7f7b6, #e96f92, #1b2947);
    background: radial-gradient(220% 105% at top center, #1b2947 10%, #75517d 40%, #e96f92 65%, #f7f67b);
    background-attachment: fixed;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

.root {
    width: 100%;
    height: 100%;
}

/* stage -- start */
.stage {
    position: relative;
    width: 100%;
    height: 100%;
}

/* stage -- end */

/* image -- start */
.img-sec {
    position: relative;
    -webkit-perspective: 1800px;
            perspective: 1800px;
}

.img-figure {
    position: absolute;
    width: 320px;
    height: 360px;
    margin: 0;
    padding: 40px;
    background-color: #fff;
    box-sizing: border-box;
    cursor: pointer;
    -webkit-transform-origin: 0 50% 0;
            transform-origin: 0 50% 0;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
    transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
    transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
    transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
}

.img-sec .is-inverse {
    -webkit-transform: translate(320px) rotateY(180deg);
            transform: translate(320px) rotateY(180deg);
}

.img-figure img {
    width: 240px;
    height: 240px;
}

figcaption {
    text-align: center;
}

figcaption .img-title {
    margin: 20px 0 0 0;
    color: #a7a0a2;
    font-size: 16px;
}

/* image -- end */

.img-back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 50px 40px;
    overflow: auto;
    color: #a7a0a2;
    font-size: 22px;
    background-color: #fff;
    box-sizing: border-box;
    -webkit-transform: rotateY(180deg) translateZ(1px);
            transform: rotateY(180deg) translateZ(1px);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* controller -- start */
.controller-nav {
    position: absolute;
    left: 0;
    bottom: 30px;
    z-index: 101;
    width: 100%;
    text-align: center;
}

.controller-unit {
    display: inline-block;
    width: 30px;
    height: 30px;
    margin: 0 5px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-color: #aaa;
    border-radius: 50%;
    -webkit-transform: scale(.5);
            transform: scale(.5);
    -webkit-transition: background-color .3s, -webkit-transform .6s ease-in-out;
    transition: background-color .3s, -webkit-transform .6s ease-in-out;
    transition: transform .6s ease-in-out, background-color .3s;
    transition: transform .6s ease-in-out, background-color .3s, -webkit-transform .6s ease-in-out;
}

.is-center {
    background-color: #888;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.is-center::after {
    color: #fff;
    font-family: "iconfont" !important;
    font-size: 24px;
    line-height: 28px;
    content: "\E6B2";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.controller-nav .is-inverse {
    background-color: #555;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

/* controller -- end  */

@-webkit-keyframes rotate {
    0% {
        -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
                transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
    }
    100% {
        -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
                transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
    }
}

@keyframes rotate {
    0% {
        -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
                transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
    }
    100% {
        -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
                transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
    }
}

.stars {
    -webkit-transform: perspective(500px);
            transform: perspective(500px);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: absolute;
    -webkit-perspective-origin: 50% 100%;
            perspective-origin: 50% 100%;
    left: 50%;
    -webkit-animation: rotate 90s infinite linear;
            animation: rotate 90s infinite linear;
    bottom: 0;
}

.star {
    width: 2px;
    height: 2px;
    background: #f7f7b6;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    z-index: 100;
    line-height: 1.25;
    text-align: left;
}

